___CSS_LOADER_EXPORT___.push([module.id, "[contenteditable=true]{display:inline-block}", "",{"version":3,"sources":["webpack://./resources/js/components/MapDetailsComponent.vue"],"names":[],"mappings":"AAyiBA,uBACA,oBACA","sourcesContent":["<template>\n  <div class=\"row\">\n    <div class=\"col-md-7\">\n      <h1\n        v-if=\"(!map || !map.title) && canEdit\"\n        :contenteditable=\"canEdit\"\n        @input=\"handleSelectInput($event, 'title')\"\n        class=\"w-100\"\n      >\n        Here's your new map!<small v-if=\"canEdit\" class=\"text-muted\">\n          (Click to edit the title)</small\n        >\n      </h1>\n      <h1\n        v-else-if=\"submit_data.title\"\n        :contenteditable=\"canEdit\"\n        @input=\"handleSelectInput($event, 'title')\"\n        class=\"w-100\"\n      >\n        {{ map.title }}\n      </h1>\n      <h1 v-else>Untitled map</h1>\n      <p\n        v-if=\"(!map || !map.description) && canEdit\"\n        :contenteditable=\"canEdit\"\n        @input=\"handleSelectInput($event, 'description')\"\n        style=\"white-space: pre-wrap\"\n      >\n        Right click (or long-tap on mobile) on the map to add markers. Click\n        here to edit the map description.\n      </p>\n      <p\n        v-else-if=\"map && map.description\"\n        :contenteditable=\"canEdit\"\n        @input=\"handleSelectInput($event, 'description')\"\n        style=\"white-space: pre-wrap\"\n        v-html=\"map.description\"\n      ></p>\n      <p v-else>This map has no description.</p>\n    </div>\n    <div class=\"col-md-5 mt-5 mt-md-0\">\n      <navigator-share\n        v-bind:on-error=\"onShareError\"\n        v-bind:url=\"map_url\"\n        v-bind:title=\"submit_data.title ? submit_data.title : 'Untitled map'\"\n        v-bind:text=\"\n          submit_data.description ? submit_data.description : 'Here\\'s my map'\n        \"\n        class=\"mb-3\"\n        ><a slot=\"clickable\" class=\"btn btn-primary btn-lg btn-block\"\n          >Share this map</a\n        ></navigator-share\n      >\n      <slot></slot>\n      <div\n        class=\"card bg-dark text-white mb-3\"\n        v-if=\"canEdit\"\n        key=\"map-settings\"\n      >\n        <div\n          class=\"card-header\"\n          data-toggle=\"collapse\"\n          data-target=\"#settingsCollapse\"\n          aria-expanded=\"false\"\n          aria-controls=\"settingsCollapse\"\n          style=\"cursor: pointer\"\n        >\n          <i class=\"fa fa-cog\"></i> Map settings\n        </div>\n        <div class=\"card-body collapse\" id=\"settingsCollapse\">\n          <div class=\"form-group row\">\n            <label for=\"password-confirm\" class=\"col-md-12 col-form-label\"\n              >Who can see this map</label\n            >\n            <div class=\"col-md-12\">\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"privacy\"\n                  id=\"exampleRadios1\"\n                  value=\"public\"\n                  :checked=\"submit_data.privacy == 'public' ? true : false\"\n                  @input=\"handleSelectInput($event, 'privacy')\"\n                />\n                <label class=\"form-check-label\" for=\"exampleRadios1\">\n                  Everyone <small>(make this map public)</small>\n                </label>\n              </div>\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"privacy\"\n                  id=\"exampleRadios2\"\n                  value=\"unlisted\"\n                  @input=\"handleSelectInput($event, 'privacy')\"\n                  :checked=\"submit_data.privacy == 'unlisted' ? true : false\"\n                />\n                <label class=\"form-check-label\" for=\"exampleRadios2\">\n                  Only people with a link\n                </label>\n              </div>\n              <div class=\"form-check disabled\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"privacy\"\n                  id=\"exampleRadios3\"\n                  value=\"private\"\n                  disabled\n                  @input=\"handleSelectInput($event, 'privacy')\"\n                />\n                <label class=\"form-check-label\" for=\"exampleRadios3\">\n                  No one\n                </label>\n              </div>\n            </div>\n          </div>\n          <div class=\"form-group row\">\n            <label\n              for=\"users_can_create_markers\"\n              class=\"col-md-12 col-form-label\"\n              >Who can create markers</label\n            >\n            <div class=\"col-md-12\">\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"users_can_create_markers\"\n                  id=\"exampleRadios4\"\n                  value=\"yes\"\n                  :checked=\"\n                    submit_data.users_can_create_markers == 'yes' ? true : false\n                  \"\n                  @input=\"handleSelectInput($event, 'users_can_create_markers')\"\n                />\n                <label class=\"form-check-label\" for=\"exampleRadios4\">\n                  Anyone\n                </label>\n              </div>\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"users_can_create_markers\"\n                  id=\"exampleRadios5\"\n                  value=\"only_logged_in\"\n                  @input=\"handleSelectInput($event, 'users_can_create_markers')\"\n                  :checked=\"\n                    submit_data.users_can_create_markers == 'only_logged_in'\n                      ? true\n                      : false\n                  \"\n                />\n                <label class=\"form-check-label\" for=\"exampleRadios5\">\n                  Only people that are logged in\n                </label>\n              </div>\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"users_can_create_markers\"\n                  id=\"exampleRadios6\"\n                  value=\"no\"\n                  @input=\"handleSelectInput($event, 'users_can_create_markers')\"\n                  :checked=\"\n                    submit_data.users_can_create_markers == 'no' ? true : false\n                  \"\n                />\n                <label class=\"form-check-label\" for=\"exampleRadios6\">\n                  No one\n                </label>\n              </div>\n              <small v-if=\"canEdit\"\n                >You can add markers to your own map regardless of this\n                setting</small\n              >\n            </div>\n          </div>\n\n          <div class=\"form-group row\">\n            <label for=\"password-confirm\" class=\"col-md-12 col-form-label\"\n              >When should new markers dissapear from the map</label\n            >\n            <div class=\"col-md-12\">\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"options.default_expiration_time\"\n                  id=\"cdet1\"\n                  value=\"4320\"\n                  :checked=\"\n                    submit_data.options.default_expiration_time == 4320\n                      ? true\n                      : false\n                  \"\n                  @input=\"\n                    handleSelectInput($event, 'options.default_expiration_time')\n                  \"\n                />\n                <label class=\"form-check-label\" for=\"cdet1\">\n                  after 3 days\n                </label>\n              </div>\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"options.default_expiration_time\"\n                  id=\"cdet2\"\n                  value=\"180\"\n                  @input=\"\n                    handleSelectInput($event, 'options.default_expiration_time')\n                  \"\n                  :checked=\"\n                    submit_data.options.default_expiration_time == 180\n                      ? true\n                      : false\n                  \"\n                />\n                <label class=\"form-check-label\" for=\"cdet2\">\n                  after 3 hours\n                </label>\n              </div>\n              <div class=\"form-check disabled\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"options.default_expiration_time\"\n                  id=\"cdet3\"\n                  :value=\"null\"\n                  @input=\"\n                    handleSelectInput($event, 'options.default_expiration_time')\n                  \"\n                  :checked=\"\n                    submit_data.options.default_expiration_time == null\n                      ? true\n                      : false\n                  \"\n                />\n                <label class=\"form-check-label\" for=\"cdet3\"> Never </label>\n              </div>\n            </div>\n          </div>\n\n          <div class=\"form-group row\">\n            <label for=\"password-confirm\" class=\"col-md-12 col-form-label\"\n              >Where can markers be placed</label\n            >\n            <div class=\"col-md-12\">\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"options.limit_to_geographical_body_type\"\n                  id=\"cdet1\"\n                  value=\"land\"\n                  :checked=\"\n                    submit_data.options.limit_to_geographical_body_type ==\n                    'land'\n                      ? true\n                      : false\n                  \"\n                  @input=\"\n                    handleSelectInput(\n                      $event,\n                      'options.limit_to_geographical_body_type'\n                    )\n                  \"\n                />\n                <label class=\"form-check-label\" for=\"cdet1\">\n                  Only on land\n                </label>\n              </div>\n              <div class=\"form-check\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"options.limit_to_geographical_body_type\"\n                  id=\"cdet2\"\n                  value=\"water\"\n                  @input=\"\n                    handleSelectInput(\n                      $event,\n                      'options.limit_to_geographical_body_type'\n                    )\n                  \"\n                  :checked=\"\n                    submit_data.options.limit_to_geographical_body_type ==\n                    'water'\n                      ? true\n                      : false\n                  \"\n                />\n                <label class=\"form-check-label\" for=\"cdet2\">\n                  Only on water\n                </label>\n              </div>\n              <div class=\"form-check disabled\">\n                <input\n                  class=\"form-check-input\"\n                  type=\"radio\"\n                  name=\"options.limit_to_geographical_body_type\"\n                  id=\"cdet3\"\n                  :value=\"null\"\n                  @input=\"\n                    handleSelectInput(\n                      $event,\n                      'options.limit_to_geographical_body_type'\n                    )\n                  \"\n                  :checked=\"\n                    submit_data.options.limit_to_geographical_body_type == null\n                      ? true\n                      : false\n                  \"\n                />\n                <label class=\"form-check-label\" for=\"cdet3\"> Anywhere </label>\n              </div>\n              <small\n                >Setting this to \"water\" or \"land\" will also drastically limit\n                how many markers can be created on this map per minute</small\n              >\n            </div>\n          </div>\n\n          <a\n            class=\"btn btn-danger btn-sm mt-3\"\n            v-if=\"canEdit\"\n            @click=\"deleteMap\"\n            :disabled=\"submit_data.loading\"\n            >Delete map</a\n          >\n        </div>\n      </div>\n      <p class=\"small\">\n        Right click (or long-tap on mobile) on the map to create a marker. You\n        can choose one of the existing labels or create your own.\n      </p>\n      <p class=\"small mb-3\" v-if=\"submit_data.options.default_expiration_time\">\n        {{ submit_data.options.default_expiration_time / 60 }} hours after\n        creating a marker it will automatically dissapear from the map.\n      </p>\n      <div\n        v-if=\"map.categories\"\n        class=\"d-flex mt-3\"\n        style=\"flex-wrap: wrap; max-height: 30vh; overflow-x: scroll\"\n      >\n        <a\n          href=\"#\"\n          class=\"badge badge-secondary mr-1 mb-1\"\n          v-for=\"category in map.categories\"\n          :key=\"category.id\"\n          >{{ category.name }}</a\n        >\n      </div>\n      <details class=\"mt-3 mb-3 small\">\n        <summary>\n          <dt class=\"d-inline text-muted\">Developer info</dt>\n        </summary>\n        <div>\n          <p>\n            Use standard API requests to get data from this map. No\n            authentication required for public and unlisted maps.\n            <a\n              href=\"https://github.com/M-Media-Group/Cartes.io/wiki/API\"\n              rel=\"noopener\"\n              target=\"_BLANK\"\n              >Read the docs</a\n            >.\n          </p>\n          <div class=\"mb-3\">\n            <p class=\"mb-0\">API <code>GET</code> map endpoint</p>\n            <code>/api/maps/{{ map.uuid }}</code>\n          </div>\n          <div class=\"mb-3\">\n            <p class=\"mb-0\">API <code>GET</code> markers endpoint</p>\n            <code>/api/maps/{{ map.uuid }}/markers</code>\n          </div>\n          <div class=\"mb-3\">\n            <p class=\"mb-0\">Embed this map on your website</p>\n            <code\n              >&lt;iframe src=\"https://cartes.io/embeds/maps/{{\n                map.uuid\n              }}?type=map\" width=\"100%\" height=\"400\"\n              frameborder=\"0\">&lt;/iframe></code\n            >\n          </div>\n          <div class=\"mb-3\">\n            <p class=\"mb-0\">\n              Shortcode, using our\n              <a target=\"_BLANK\" href=\"https://wordpress.org/plugins/cartes/\"\n                >WordPress plugin</a\n              >\n            </p>\n            <code>[cartes_map uuid=\"{{ map.uuid }}\"]</code>\n          </div>\n          <p>\n            When using the API or embedding the map, you must attribute this\n            website on your front-end.\n          </p>\n        </div>\n      </details>\n    </div>\n  </div>\n</template>\n<script lang=\"ts\">\nimport NavigatorShare from \"vue-navigator-share\";\nimport copy from \"copy-to-clipboard\";\nimport axios from \"axios\";\n\nexport default {\n  props: [\"map_id\", \"map_token\", \"map\"],\n  components: {\n    NavigatorShare,\n  },\n  data() {\n    return {\n      submit_data: {\n        title: this.map.title,\n        description: this.map.description,\n        token: this.map_token,\n        privacy: this.map.privacy,\n        users_can_create_markers: this.map.users_can_create_markers,\n        loading: false,\n        options: {\n          default_expiration_time:\n            this.map.options && this.map.options.default_expiration_time\n              ? this.map.options.default_expiration_time\n              : null,\n          limit_to_geographical_body_type:\n            this.map.options && this.map.options.limit_to_geographical_body_type\n              ? this.map.options.limit_to_geographical_body_type\n              : null,\n        },\n      },\n    };\n  },\n  mounted() {\n    if (localStorage[\"map_\" + this.map_id] && !this.submit_data.token) {\n      this.submit_data.token = localStorage[\"map_\" + this.map_id];\n    } else if (this.submit_data.token) {\n      localStorage[\"map_\" + this.map_id] = this.submit_data.token;\n    }\n  },\n  computed: {\n    map_url() {\n      return window.location.href;\n    },\n    canEdit() {\n      return this.submit_data.token ? true : false;\n    },\n  },\n\n  watch: {\n    title: function (newQuestion, oldQuestion) {\n      this.submit_data.title = \"Waiting for you to stop typing...\";\n      this.debouncedGetAnswer();\n    },\n  },\n  created: function () {\n    // _.debounce is a function provided by lodash to limit how\n    // often a particularly expensive operation can be run.\n    // In this case, we want to limit how often we access\n    // yesno.wtf/api, waiting until the user has completely\n    // finished typing before making the ajax request. To learn\n    // more about the _.debounce function (and its cousin\n    // _.throttle), visit: https://lodash.com/docs#debounce\n    this.debouncedGetAnswer = _.debounce(this.submitForm, 950);\n  },\n  methods: {\n    onShareError(e) {\n      copy(this.map_url, {\n        message: \"Press #{key} to copy\",\n        onCopy: this.$notify({\n          type: \"success\",\n          title: \"Link copied\",\n          duration: 5000,\n        }),\n      });\n    },\n    handleSelectInput(val, type) {\n      if (type == \"privacy\" || type == \"users_can_create_markers\") {\n        this.submit_data[type] = val.target.value;\n      } else if (type == \"options.default_expiration_time\") {\n        this.submit_data.options.default_expiration_time = val.target.value;\n      } else if (type == \"options.limit_to_geographical_body_type\") {\n        this.submit_data.options.limit_to_geographical_body_type =\n          val.target.value;\n      } else {\n        this.submit_data[type] = val.target.innerText;\n      }\n      this.debouncedGetAnswer();\n    },\n    checkForLocalStorageKey(id) {\n      if (localStorage[\"map_\" + id]) {\n        return true;\n      }\n      return false;\n    },\n    submitForm(event) {\n      this.submit_data.loading = true;\n      axios\n        .put(\"/api/maps/\" + this.map_id, this.submit_data) // change this to post )\n        .then((res) => {\n          this.submit_data.loading = false;\n          this.$emit(\"map-update\", res.data);\n        })\n        .catch((error) => {\n          this.submit_data.loading = false;\n          console.log(error);\n          if (error.response.data.errors) {\n            var message = Object.entries(error.response.data.errors)\n              .map(\n                ([error_name, error_value], i) =>\n                  `${error_name}: ${error_value[0]} | `\n              )\n              .join(\"\\n\");\n          } else {\n            var message = error.response.data.message;\n          }\n          alert(message);\n        });\n    },\n    deleteMap(id) {\n      if (!confirm(\"You'll never see this map again. Are you sure?\")) {\n        return;\n      }\n      this.submit_data.loading = true;\n      axios\n        .delete(\"/api/maps/\" + this.map_id, {\n          data: { token: this.submit_data.token },\n        })\n        .then((res) => {\n          localStorage.removeItem(\"map_\" + this.map_id);\n          alert(\"This map has gone with the wind!\");\n          window.location.href = \"/\";\n          this.submit_data.loading = false;\n        })\n        .catch((error) => {\n          this.submit_data.loading = false;\n          //console.log(error);\n          alert(error.message);\n        });\n    },\n  },\n};\n</script>\n<style>\n*[contenteditable=\"true\"] {\n  display: inline-block;\n}\n</style>"],"sourceRoot":""}]);
